@mixin image-basic($width,$height,$url,$size) {
  width: $width;
  height: $height;
  background: url($url) no-repeat center;
  background-color: inherit;
  background-size: $size;
}
@mixin image-position($top,$right,$bottom,$left,$position) {
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
  position: $position;
}
