@import "../base/variable.module";
@import "../base/icons";

.container {
  aside {
    width: 25%;
    float: left;
    padding: 0 0 5vh 5vw;
    box-sizing: border-box;
    li {
      list-style: none;
      margin-bottom: 5vh;
      font-size: 1.2vw;
      &:hover {
        cursor: pointer;
        text-decoration: underline $primary-bg-color;
      }
    }
  }
}
.container {
  width: 100vw;
  height: 90vh;
  background-color: inherit;
  .header-shadow {
    width: 100%;
    height: 1vh;
    box-shadow: 0 5px 5px $primary-shadow;
  }
}
.filters {
  width: 90%;
  font-size: 1.5vw;
  padding: 2vh 0 2vh 5vw;
  .searchInput {
    float: right;
    position: relative;
    input{
      border: 1px solid $theme-color-dark;
      border-radius: 1vh;
      //padding: 5px;
      font-size: 1vw;
      width: 15vw;
      padding: 0.5vw;
    }
    .fa-magnifying-glass{
      position: absolute;
      right: 0.5vw;
      bottom: 2vh;
      color: $secondary-text-color;
    }
  }
  .fa-circle-plus{
    @include icon-style($primary-shadow);
    float: right;
    margin-top: 1.3vh;
    margin-right: 1vw;
  }
}
.dialog{
  background-color: pink;
}
.content {
  height: 72vh;
  overflow-y: scroll;
  scroll-padding-top: 10vh;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  column-gap: 2vw;
  justify-content: space-evenly;
}
h1 {
  margin: 1vh;
}
body.dark {  
    .header-shadow {
      box-shadow: 0 5px 5px $primary-shadow-dark;
    }
}
