@use '../base/variable.module' as variable;

.card {
  font-size: 1.1vw;
  width: 18vw;
  margin: 2vh 0;
  padding: 10px;
  overflow: hidden;

  &:hover {
    border-radius: 5px;
    box-shadow: 0 5px 20px variable.$primary-shadow;
    
  }
  .thumbnail {
    overflow: hidden;
    width: 100%;
    border-radius: 5px;
    img {
      width: 18vw;
      height: 30vh;
      object-fit: cover;
      border-radius: 5px;
      transition: all 0.2s cubic-bezier(0.15, 0.69, 0.83, 0.67);
      &:hover {
        transform: scale(1.05);
        box-sizing: border-box;
      }
    }
  }
  div:not(:first-child) {
    width: 10vw;
    float: left;
  }
  button {
    float: right;
    &:hover {
      cursor: pointer;
    }
  }
}
body.dark {
  .card {
    &:hover {
      box-shadow: 0 2px 20px variable.$primary-shadow-dark;
    }
  }
}